* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Futura PT';
}


.section {
    background-color: #000000;
}

.container {
    padding: 0 16px;
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
}

img {
    max-width: 100%;
    height: auto;
    width: 100%;
}

body {
    color: white;
}

.header {
    background-color: #000000;
}

.footer {
    background-color: #000000;
}

.info__container {
    padding-bottom: 10px;
    max-width: 1400px;
    width: 100%;

    .info__nav {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .info-rhomb {
        width: 61px;
        height: 61px;
        background: linear-gradient(176.47deg, #FF3333 -36.48%, rgba(255, 51, 51, 0) 95.83%);
        transform: rotate(45deg);
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 20px;
    }

    .rhomb-text {
        transform: rotate(-45deg);
        color: rgba(255, 255, 255, 1);
        font-size: 13px;
        text-align: center;
    }

    .info__nav-list {
        display: flex;
        justify-content: space-between;
        list-style-type: none;
        max-width: 650px;
        width: 100%;
        text-transform: uppercase;
        font-size: 17px;
        font-weight: 400;
        line-height: 22px;
        margin-left: 147px;
        margin-right: 100px;
    }

    .info__nav-icons {
        display: flex;
        flex-shrink: 0;
        gap: 20px;
        margin-right: 102px;
    }


    .info__nav-login {
        display: flex;
        align-items: center;
        gap: 10px;
        text-decoration: none;
    }

    .info__nav-logintext {
        color: rgba(255, 51, 51, 1);
    }
}

.intro__payblock {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(/src/img/background_intro.png);
    background-repeat: no-repeat;
    padding-top: 104px;

    .intro__pay {
        display: flex;
        align-items: center;
        gap: 50px;

        .intro__pay-text {
            font-family: "Lato";
            font-size: 110px;
            font-weight: 500;

        }

        .intro__pay-rhomb {
            width: 100px;
            height: 100px;
            background: linear-gradient(176.47deg, #FF3333 -36.48%, rgba(255, 51, 51, 0) 95.83%);
            transform: rotate(45deg);
            display: flex;
            align-items: center;
            justify-content: center;

            .intro__pay-rhombtext {
                transform: rotate(-45deg);
                color: rgba(255, 255, 255, 1);
                font-size: 22px;
            }
        }
    }

    .intro__text {
        font-family: "Lato";
        font-size: 110px;
        font-weight: 500;
        /* margin-left: 100px; */
    }
}

.intro__block {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;
    margin-top: 50px;
    padding-bottom: 210px;

    .intro__block-text {
        font-size: 24px;
        font-weight: 400;
        width: 304px;
        text-align: justify;
        line-height: 31.2px;
    }

    .intro__block-button {
        width: 244px;
        height: 70px;
        background-color: #FF3333;
        font-size: 24px;
        color: white;
    }
}

.games {
    background-color: white;

    .games_container {
        max-width: 1920px;

        .games_list {
            display: flex;
            justify-content: center;
            margin-top: 20px;
            gap: 100px;
        }

        .games__list-img {
            width: 80px;
            height: 42px;
        }
    }
}

.goals {
    background-image: radial-gradient(50% 50% at 50% 50%, rgba(255, 51, 51, 0.5) 0%, rgba(255, 51, 51, 0) 100%);
    background-size: 100% 712px;
    background-repeat: no-repeat;

    .goals__info {
        margin-top: 25px;

        .goals__title {
            font-family: "Lato", sans-serif;
            font-size: 100px;
            font-weight: 500;
            line-height: 100px;
            text-align: center;
            padding-top: 100px;
        }

        .goals__text-intro {
            font-size: 22px;
            text-align: center;
            margin-top: 20px;
            margin-bottom: 60px;
        }

        .goals__blocks {
            display: flex;
            justify-content: center;
            gap: 30px;

            .goals__blocks-article {
                width: 319px;
                height: 282px;
                text-align: center;
                padding-top: 30px;
                font-size: 22px;
                line-height: 28px;
                margin-bottom: 100px;
                display: flex;
                flex-direction: column;
                align-items: center;
                background: linear-gradient(130.66deg, rgba(255, 255, 255, 0.3) -19.06%, rgba(255, 255, 255, 0) 31.37%), linear-gradient(0deg, #000000, #000000);
                border: 1px solid;
                border-image-source: linear-gradient(131.37deg, rgba(47, 46, 46, 0.5) 0%, rgba(255, 255, 255, 0) 67.7%);
                box-shadow: 30px 30px 36px 0px rgba(21, 21, 21, 0.5) inset;

                .goals__rhomb {
                    width: 61px;
                    height: 61px;
                    background: linear-gradient(176.47deg, #FF3333 -36.48%, rgba(255, 51, 51, 0) 95.83%);
                    transform: rotate(45deg);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-bottom: 20px;

                    .goals__rhombtext {
                        transform: rotate(-45deg);
                        font-size: 40px;
                    }
                }

                .goals__article-text {
                    max-width: 291px;
                }
            }
        }

    }

    .goals__footer {
        padding: 300px 0;
        background-image: url(/src/img/stacking/Ellipse.png);
        background-repeat: no-repeat;
        background-position: 42% 50%;

        .goals__footer-text {
            margin: 0 auto;
            font-size: 50px;
            font-weight: 400;
            line-height: 65px;
            text-align: center;
            width: 933px;
        }
    }
}


.diagram {
    background-color: white;

    .diagram__container {
        color: #000000;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .diagram__text {
            max-width: 423px;
            width: 100%;

            .diagram__text-vip {
                font-size: 100px;
                font-weight: 400;
                line-height: 130px;
            }

            .diagram__text-usdt {
                font-size: 90px;
                font-weight: 400;
                line-height: 117px;
            }
        }

        .diagram__block {
            width: 435px;
            margin: 100px 0;

            .diagram__block-img {
                width: 100%;
            }

            .diagram__block-text {
                margin-top: 15px;
                font-size: 24px;
                font-weight: 400;
            }
        }
    }
}

.stacking__container {
    padding-bottom: 100px;

    .stacking__info {
        margin: 30px auto;
        max-width: 1200px;
        width: 100%;
        background-image: url('/src/img/stacking/background_stacking.png'), url('/src/img/stacking/Ellipse.png');
        background-repeat: no-repeat;
        background-position: center;
        padding: 70px 0;

        .stacking__info-title {
            font-family: "Lato", sans-serif;
            font-size: 110px;
            font-weight: 500;
            line-height: 143px;
            text-align: center;
        }

        .stacking__info-intro {
            font-size: 24px;
            font-weight: 400;
            line-height: 31.2px;
            width: 250px;
        }

        .stacking__info-footer {
            font-size: 24px;
            font-weight: 400;
            line-height: 31.2px;
            width: 320px;
            margin-left: auto;
        }
    }

    .stacking__blocks {
        display: flex;
        justify-content: center;
        gap: 20px;

        .stacking__blocks-article {
            width: 319px;
            height: 206px;
            text-align: center;
            padding: 19px 30px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            font-size: 20px;
            font-weight: 400;
            background: linear-gradient(130.66deg, rgba(255, 255, 255, 0.3) -19.06%, rgba(255, 255, 255, 0) 31.37%);
            border: 1px solid;
            border-radius: 8px;
            border-image-source: linear-gradient(131.37deg, rgba(47, 46, 46, 0.5) 0%, rgba(255, 255, 255, 0) 67.7%);
            box-shadow: 30px 30px 36px 0px rgba(21, 21, 21, 0.5) inset;
            box-shadow: -5px -5px 20px 0px rgba(255, 255, 255, 0.3);

            .stacking-blocks-intro {
                font-size: 24px;
                font-weight: 600;
                margin-bottom: 10px;
            }
        }
    }
}

.packets {
    background: white;

    .packets__container {
        color: #000000;
        padding-bottom: 100px;

        .packets__intro {
            display: flex;
            justify-content: center;
            gap: 248px;
            margin-bottom: 78px;

            .packets__intro-title {
                font-family: "Lato", sans-serif;
                font-size: 110px;
                font-weight: 500;
                margin: 100px 0;
            }

            .packets__intro-text {
                font-size: 24px;
                font-weight: 400;
                line-height: 31px;
                text-align: left;
                width: 509px;
                padding-top: 172px;
            }
        }

        .packets__blocks {
            display: flex;
            justify-content: center;
            gap: 40px;
            position: relative;

            .packets__blocks-article {
                width: 250px;
                text-align: center;
                border: 2px solid rgba(255, 51, 51, 1);
                border-radius: 8px;
                position: relative;

                .packets__article-title {
                    padding-top: 63px;
                    font-size: 50px;
                    font-weight: 600;
                    line-height: 65px;
                    color: rgba(255, 51, 51, 1);
                }

                .packets__article-percent {
                    padding: 10px 0;
                    font-size: 24px;
                    font-weight: 400;
                    line-height: 31px;
                }

                .packets__article-time {
                    font-size: 24px;
                    font-weight: 400;
                    line-height: 31px;
                }

                .packets__article-available {
                    padding: 50px 0 30px;
                    font-size: 22px;
                    font-weight: 400;
                    line-height: 28px;
                }

                .packets__article-image {
                    position: absolute;
                    top: -30px;
                    left: 50%;
                    transform: translate(-50%);

                    .packets__image-style {
                        width: 60px;
                        height: 60px;
                        background: rgba(255, 51, 51, 1);
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        transform: rotate(45deg);

                        .packets__image-text {
                            transform: rotate(-45deg);
                            color: white;
                            font-size: 24px;
                        }
                    }
                }
            }
        }

        .packets__blocks-direction-left {
            width: 60px;
            height: 60px;
            background: rgba(255, 51, 51, 1);
            position: absolute;
            right: 0;
            top: 50%;
            transform: translate(90px, -50%);
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .packets__blocks-direction-right {
            width: 60px;
            height: 60px;
            background: rgba(255, 51, 51, 1);
            position: absolute;
            left: 0;
            top: 50%;
            transform: translate(-90px, -50%);
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}

.referal {
    background: #000000;
    padding-bottom: 100px;

    .referal__intro {
        margin: 0 auto;
        padding: 29px 0 31px;
        background-image: url(/src/img/stacking/background_stacking.png), url(/src/img/stacking/Ellipse.png);
        background-repeat: no-repeat;
        background-position: center;
        padding: 171px 0 69px;
        display: flex;
        flex-direction: column;
        margin-bottom: 31px;

        .referal__intro-title {
            font-family: Lato;
            font-size: 110px;
            font-weight: 500;
            line-height: 143px;
            text-align: center;
            margin-bottom: 40px;
        }

        .referal__intro-text {
            font-size: 24px;
            font-weight: 400;
            line-height: 31.2px;
            max-width: 359px;
            width: 100%;
            align-self: flex-end;
            margin-right: -113px;
        }
    }

    .referal__footer {
        display: flex;
        gap: 30px;

        .referal-item {
            max-width: 554px;
            width: 100%;
            padding: 30px;
            display: flex;
            flex-direction: column;
            border: 1px solid;
            border-image-source: linear-gradient(131.37deg, rgba(47, 46, 46, 0.5) 0%, rgba(255, 255, 255, 0) 67.7%);
            box-shadow: 30px 30px 36px 0px rgba(21, 21, 21, 0.5) inset, -5px -5px 20px 0px rgba(255, 255, 255, 0.3);
            border-radius: 8px;
            background: linear-gradient(130.66deg, rgba(255, 255, 255, 0.3) -19.06%, rgba(255, 255, 255, 0) 31.37%);

            .referal-item__title {
                font-size: 36px;
                font-weight: 600;
                line-height: 43px;
                margin-bottom: 30px;
            }

            .referal-item__text {
                font-size: 24px;
                font-weight: 400;
                line-height: 31px;
                margin-bottom: 80px;
            }

            .referal-item__percent {
                display: flex;
                gap: 66px;

                .referal-item__percent-item {
                    width: 148px;
                    flex-shrink: 0;

                    .referal-item__percent-title {
                        font-size: 26px;
                        font-weight: 400;
                        line-height: 34px;
                    }

                    .referal-item__percent-text {
                        font-size: 20px;
                        font-weight: 400;
                        line-height: 26px;
                        margin-bottom: 50px;
                    }
                }

                .referal-item__percent-item-2 {
                    width: 268px;

                    .referal-item__percent-title {
                        font-size: 26px;
                        font-weight: 400;
                        line-height: 34px;
                    }

                    .referal-item__percent-text {
                        font-size: 20px;
                        font-weight: 400;
                        line-height: 26px;
                        margin-bottom: 50px;
                    }
                }
            }

            .referal-item__example {
                display: flex;
                gap: 30px;

                .referal-item__example-figure {
                    width: 42px;
                    height: 42px;
                    background-color: rgba(255, 51, 51, 1);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transform: translate(0,-20px) rotate(45deg) ;
                    

                    .referal-item__example-img {
                        transform: rotate(-45deg);
                    }
                }

                .referal-item__example-text {
                    font-size: 20px;
                    font-weight: 400;
                    line-height: 26px;
                    width: 327px;
                    margin-bottom: 66px;
                }
            }

            .referal-item__footer {
                margin-left: auto;
            }
        }
    }
}


.swiper {
    width: 600px;
    height: 300px;
}